import React from "react";
import { graphql } from "gatsby";
import { initializeApp } from "firebase/app";
import { getFirestore } from "firebase/firestore/lite";
import Layout from "@components/layout";
import Seo from "@components/seo";
import BreadCrumb from "@components/common/breadcrumb";
import MoveForward from "@components/common/move-forward";
import PortfolioTwo from "@screens/portfolio/index-portfolio";

const PortfolioPage = (props) => {
  const app = initializeApp(props?.data?.site?.siteMetadata?.firestore);
  const db = getFirestore(app);
  const breadcrumbLinks = [
    { text: "Home", link: "/" },
    { text: "Clone Scripts", link: null },
  ];
  return (
    <Layout>
      <BreadCrumb items={breadcrumbLinks} />
      <PortfolioTwo db={db} />
      <MoveForward db={db} />
    </Layout>
  );
};

export const Head = (props) => {
  const { data } = props;
  const fileUrl =
    data?.cloneScriptImg?.childImageSharp?.gatsbyImageData?.images?.fallback
      ?.src;

  return (
    <Seo
      title="Clone Scripts | Justtry Technologies"
      metaTitle="Justtry Technologies Clone Scripts"
      description="Discover customizable clone scripts to quickly launch your own apps and websites. Save time and costs with our feature-rich, scalable solutions."
      image={fileUrl}
    ></Seo>
  );
};

export default PortfolioPage;

export const queryData = graphql`
  query MyQuery {
    cloneScriptImg: file(relativePath: { eq: "clone-script.png" }) {
      childImageSharp {
        gatsbyImageData
      }
    }
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
